"use client";

import { useEffect, useState } from "react";
import { getDiscoverMovies } from "@/api/tmdb";
import { MovieCard } from "@/components/MovieCard";
import { Filters } from "@/components/Filters";

export default function MoviesPage() {
    const [movies, setMovies] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        async function loadMovies() {
            setLoading(true);
            const data = await getDiscoverMovies({ sort_by: "popularity.desc" });
            if (data?.results) setMovies(data.results);
            setLoading(false);
        }
        loadMovies();
    }, []);

    return (
        <main className="min-h-screen pt-24 pb-20 bg-bg-main">
            <div className="container mx-auto px-6 lg:px-20 mb-8">
                <h1 className="text-4xl font-display font-bold text-white mb-2">Movies</h1>
                <p className="text-text-secondary">Discover the best movies from around the world.</p>
            </div>

            <Filters />

            <div className="container mx-auto px-6 lg:px-20 mt-10">
                {loading ? (
                    <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-5 gap-6">
                        {[...Array(10)].map((_, i) => (
                            <div key={i} className="aspect-[2/3] bg-white/5 rounded-xl animate-pulse" />
                        ))}
                    </div>
                ) : (
                    <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-5 gap-y-10 gap-x-6">
                        {movies.map((movie) => (
                            <MovieCard key={movie.id} movie={movie} />
                        ))}
                    </div>
                )}
            </div>
        </main>
    );
}
